}

@media screen and (min-width: 320px, ) {
  .title {
    position: relative;
    top: 150px;
    left: 10px;
    font-size: 20px;
  }
   .titleTwo {
    position: relative;
    top: 150px;
    left: 70px;
    font-size: 20px;
}
}
@media screen and (min-width: 321px) {
  .title {
    font-size: 30px;
    position: absolute;
    bottom: 650px;
    left: 105px;
    font-size: 50px;
  }
   .titleTwo {
    font-size: 30px;
    position: absolute;
    bottom: 525px;
